@import "variables.scss";
@import "main-nav.scss";
@import "intro.scss";
@import "download-info.scss";
@import "promo.scss";
@import "features.scss";
@import "carousel.scss";
@import "price-list.scss";
@import "contact.scss";
@import "map.scss";
@import "footer.scss";
html {
    font-size: $html-size-base;
    min-width: 320px;
}
body {
    margin: 0;
    font-family: $font-family-base;
    font-size: $font-size;
    line-height: $line-height;
    color: $blue;
    background-color: $white;
    overflow-x: hidden;
}
button,
input,
select,
textarea {
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}
a {
    color: $pink;
    text-decoration: none;
}
.container {
    margin: 0 auto;
    width: 100%;
    max-width: 960px;
}
